import interfaceurl from '@/utils/interface.js'
import authPage from '@/components/authorization-page.vue' //引入授权窗体
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	components: {
		authPage,
		navigationBar
	},
	computed: mapState(['userInfos']),
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: 'VIP' //导航栏文字
			},
			rankList: [], //佣金排名
			userInfo: {}, //获取用户信息
			userDetail: {}, //获取用户详情
			userNumber: {} //获取用户的粉丝和会员人数
		}
	},
	watch:{
		userInfos(val) {
			//用户的授权信息存本地，全局变量是为了第一次授权时快速响应
			if(val) {
				this.userInfo = uni.getStorageSync('userInfo')
			}
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo')
		if(uni.getStorageSync('token')) {
			this.getUserDetail();
			this.getUserNumber();
			this.getRanking()
		}
	},
	methods: {
		//获取用户详情
		getUserDetail() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.showDetail, {}).then((res) => {
				that.userDetail = res.data
			});
		},
		//获取用户的粉丝和会员人数
		getUserNumber() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.surveyList, {}).then((res) => {
				that.userNumber = res.data
			});
		},
		//获取排名列表
		getRanking() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.teamRankingList, {}).then((res) => {
				that.rankList = res.data
			});
		}
	}
}
